// Recommendation state management
import { create } from 'zustand';
export const useRecommendationStore = create((set, get) => ({
    recommendedItems: [],
    recommendedOutfits: [],
    savedOutfits: [],
    wishlistItems: [],
    cartItems: [],
    viewedItems: [],
    context: null,
    loading: false,
    error: null,
    setRecommendedItems: (items) => {
        set({ recommendedItems: items });
    },
    setRecommendedOutfits: (outfits) => {
        set({ recommendedOutfits: outfits });
    },
    setSavedOutfits: (outfits) => {
        set({ savedOutfits: outfits });
    },
    addRecommendedItem: (item) => {
        set((state) => ({
            recommendedItems: [...state.recommendedItems, item]
        }));
    },
    addRecommendedOutfit: (outfit) => {
        set((state) => ({
            recommendedOutfits: [...state.recommendedOutfits, outfit]
        }));
    },
    addToWishlist: (item) => {
        // Check if item is already in wishlist
        const exists = get().wishlistItems.some((wishlistItem) => wishlistItem.itemId === item.itemId);
        if (!exists) {
            set((state) => ({
                wishlistItems: [...state.wishlistItems, item]
            }));
        }
    },
    removeFromWishlist: (itemId) => {
        set((state) => ({
            wishlistItems: state.wishlistItems.filter((item) => item.itemId !== itemId)
        }));
    },
    addToCart: (item) => {
        // Check if item is already in cart
        const existingItemIndex = get().cartItems.findIndex((cartItem) => cartItem.itemId === item.itemId);
        if (existingItemIndex >= 0) {
            // Update quantity if already in cart
            set((state) => {
                const updatedCartItems = [...state.cartItems];
                updatedCartItems[existingItemIndex].quantity += item.quantity;
                return { cartItems: updatedCartItems };
            });
        }
        else {
            // Add new item to cart
            set((state) => ({
                cartItems: [...state.cartItems, item]
            }));
        }
    },
    removeFromCart: (itemId) => {
        set((state) => ({
            cartItems: state.cartItems.filter((item) => item.itemId !== itemId)
        }));
    },
    updateCartItemQuantity: (itemId, quantity) => {
        set((state) => ({
            cartItems: state.cartItems.map((item) => item.itemId === itemId ? Object.assign(Object.assign({}, item), { quantity }) : item)
        }));
    },
    addViewedItem: (itemId) => {
        if (!get().viewedItems.includes(itemId)) {
            set((state) => ({
                viewedItems: [...state.viewedItems, itemId]
            }));
        }
    },
    saveOutfit: (outfit) => {
        // Check if outfit is already saved
        const exists = get().savedOutfits.some((savedOutfit) => savedOutfit.outfitId === outfit.outfitId);
        if (!exists) {
            set((state) => ({
                savedOutfits: [...state.savedOutfits, outfit]
            }));
        }
    },
    removeSavedOutfit: (outfitId) => {
        set((state) => ({
            savedOutfits: state.savedOutfits.filter((outfit) => outfit.outfitId !== outfitId)
        }));
    },
    setContext: (context) => {
        set({ context });
    },
    clearRecommendations: () => {
        set({
            recommendedItems: [],
            recommendedOutfits: [],
            context: null
        });
    },
    setLoading: (loading) => {
        set({ loading });
    },
    setError: (error) => {
        set({ error });
    },
    getItemsByCategory: (category) => {
        return get().recommendedItems.filter(item => item.category === category);
    },
    getItemsSortedByMatchScore: () => {
        return [...get().recommendedItems].sort((a, b) => b.matchScore - a.matchScore);
    },
    getOutfitById: (id) => {
        return get().recommendedOutfits.find(outfit => outfit.id === id);
    },
    getItemById: (id) => {
        return get().recommendedItems.find(item => item.id === id);
    },
    updateItem: (id, updates) => {
        set((state) => ({
            recommendedItems: state.recommendedItems.map(item => item.id === id ? Object.assign(Object.assign({}, item), updates) : item)
        }));
    }
}));
export default useRecommendationStore;
