// Chat state management
import { create } from 'zustand';
import { v4 as uuidv4 } from 'uuid';
import { MessageType } from '../types/index';
export const useChatStore = create((set, get) => ({
    messages: [],
    isOpen: true,
    isMinimized: false,
    isLoading: false,
    error: null,
    currentView: 'chat',
    addMessage: (message) => {
        const id = uuidv4();
        const timestamp = new Date();
        const newMessage = Object.assign(Object.assign({}, message), { id, timestamp });
        // Using type assertion to work around type compatibility issues
        set((state) => ({
            messages: [...state.messages, newMessage]
        }));
        return id;
    },
    addTextMessage: (text, sender) => {
        const senderValue = typeof sender === 'string' ? sender : sender;
        const message = {
            type: MessageType.TEXT,
            sender: senderValue,
            text
        };
        return get().addMessage(message);
    },
    updateMessage: (id, updates) => {
        // Using type assertion to work around type compatibility issues
        set((state) => ({
            messages: state.messages.map((msg) => msg.id === id ? Object.assign(Object.assign({}, msg), updates) : msg)
        }));
    },
    removeMessage: (id) => {
        set((state) => ({
            messages: state.messages.filter((msg) => msg.id !== id)
        }));
    },
    clearMessages: () => {
        set({ messages: [] });
    },
    toggleOpen: () => {
        const isCurrentlyOpen = get().isOpen;
        set({
            isOpen: !isCurrentlyOpen,
            isMinimized: false
        });
    },
    toggleMinimize: () => {
        const isCurrentlyMinimized = get().isMinimized;
        set({ isMinimized: !isCurrentlyMinimized });
    },
    setLoading: (isLoading) => {
        set({ isLoading });
    },
    setError: (error) => {
        set({ error });
    },
    setCurrentView: (view) => {
        set({ currentView: view });
    },
    setIsOpen: (isOpen) => {
        set({ isOpen });
    }
}));
